<template>
    <div class="login">
      <h1>這是 Vue.js App</h1>
      <footer>
        <hr style="border:1px solid;">
        <p class="text-secondary">{{ nowDate }}</p>
      </footer>
    </div>
  </template>
  
  <script>
  
  export default {
    name: 'LoginView',
    data() {
      return {
        nowDate: "zxc"
      }
    },
    methods: {
      upNowDate() {
        var dt = new Date();
        var { yyyy, MM, dd, HH, mi, ss } = {
            yyyy: dt.getFullYear(),
            MM: (dt.getMonth() + 1).toString().padStart(2, '0'),
            dd: dt.getDate().toString().padStart(2, '0'),
            HH: dt.getHours().toString().padStart(2, '0'),
            mi: dt.getMinutes().toString().padStart(2, '0'),
            ss: dt.getSeconds().toString().padStart(2, '0'),
        };
        this.nowDate = `${yyyy}-${MM}-${dd} ${HH}:${mi}:${ss}`;
        // console.log(this.nowDate);
      }
    },
    mounted: function() {
      // 在Vue實例創建後，開始定時更新當前時間
      this.upNowDate();
      setInterval(this.upNowDate, 5000);
    }
  }
  </script>
  